/*HEADER*/
/*enquete.css*/
header{
    background: #004694; flex-wrap: wrap;flex-direction: column; align-items: center;
    justify-content: space-around; display: flex; width: 100vw; height: 65px;
}

header img{
    height: 70%;
}


@media (min-width: 1000px) {
/*FOOTER*/
footer{
    align-items: center; justify-content: space-evenly; position: fixed;display: flex;bottom: 0vh;
    background: #004694;width: 100%;
}
footer section{
    height: 40%; display: flex; align-items: center; justify-content: space-evenly;
}
footer section h1{
    font-size: 15px;  color: #fff;
}

footer img{
    margin: 1vw; height: 100%;
}

footer #contributors{
    display: flex; flex-direction: column;
}

footer #contributors div{
    display: flex;flex-direction: row;
}

footer #contributors img{
    height: 60%;
}
}

@media (max-width: 999px) {
    #navbar{
        display: none;
    }

    #navbarMobile{
        display: flex; background: palegreen;align-items: center; justify-content: center;
        
    }
    #navbarMobile ul{
        display: flex; justify-content: center;  align-items: center;
    }
    #navbarMobile ui{
        align-items: center;justify-content: center;background: red;padding: 5px;
    }
    /*FOOTER*/
    
    footer{
        align-items: center;justify-content: space-evenly;flex-direction: column;
        position: relative;display: flex; bottom: 0vh; background: #004694; width: 100%;
    }
    footer section{
        height: 40%; display: flex;
        align-items: center; justify-content: space-evenly;
    }
    footer section h1{
        font-size: 8px;
        color: #fff;
    }
    
    footer img{
        margin: 1vw;
        height: 50%;
    }
    
    footer #contributors{
        display: flex; align-items: center;  justify-content: center;
        flex-direction: column; background: red;
    }
    footer #contributors h1{
        padding-top: 10px; margin-bottom: -5px;
    }
    
    footer #contributors div{
        display: flex; align-items: center; justify-content: center; flex-direction: row;
    }
    
    footer #contributors img{
        height: 40%;
    }
    }

