/*countdown.css*/

@import url('https://fonts.googleapis.com/css?family=Lato');

* {
    box-sizing: border-box;
}

body {
    font-family: 'Lato', sans-serif; background-color: #e7ebf0;
}

h5 {
    letter-spacing: 1px; text-align: center; text-transform: uppercase;
}

.countdown-wrapper {
    display: flex;  align-items: flex-start; justify-content: center;  
     flex-wrap: wrap;height: 50px;
}

.countdown-item {
    color: #004694; font-size: 40px;  display: flex;  align-items: center; 
     justify-content: center;flex-direction: column; line-height: 30px; margin: 10px;
    padding-top: 10px; position: relative; width: 100px; height: 100px;
}

.countdown-item span {
    color: #004694;  font-size: 12px;
    font-weight: 600;  text-transform: uppercase;
}

.countdown-svg {
    position: absolute;
    top: 0;  left: 0; width: 100px; height: 100px;
}

